/* Specification */
body {
  margin: 0;
}
body button {
  outline: none;
}
/* Header */
#nav-icon1 {
  display: none;
}
.menu__mobile {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  background: #4b3b4c;
  width: 100%;
  height: 300px;
  z-index: 100;
}
.menu__mobile ul {
  list-style: none;
  text-align: center;
  padding-left: 0;
  padding-top: 30px;
}
.menu__mobile li {
  margin-bottom: 30px;
}
.menu__mobile li:last-child {
  margin-bottom: 0;
}
.menu__mobile a {
  font-size: 20px;
  color: #fff;
  font-family: 'Montserrat';
  text-decoration: none;
}
.header__fixed {
  position: fixed;
  background: #fff;
  width: 100%;
  height: 110px;
  z-index: 100;
}
.header__menu {
  margin: 0 auto;
  max-width: 1120px;
  display: flex;
  padding: 30px 0;
  align-items: center;
  justify-content: space-between;
}
.header__menu--logo {
  display: flex;
  align-items: center;
}
.header__menu--logo img:hover {
  -webkit-transform: rotateZ(20deg);
  -ms-transform: rotateZ(20deg);
  transform: rotateZ(20deg);
}
.header__menu--logo h3 {
  color: #333333;
  font-family: 'Lato';
  font-size: 24px;
  font-weight: 700;
  line-height: 16px;
  text-transform: uppercase;
  margin-left: 15px;
}
.header__menu--logo span {
  color: #8d81ac;
}
.header__menu--nav {
  display: flex;
}
.header__menu--nav ul {
  display: flex;
  list-style: none;
}
.header__menu--nav li {
  margin-right: 45px;
}
.header__menu--nav li:last-child {
  margin-right: 35px;
}
.header__menu--navLink {
  width: 498px;
  height: 16px;
  color: #635c73;
  font-family: 'Montserrat';
  font-size: 16px;
  font-weight: 700;
  line-height: 15px;
  text-decoration: none;
}
.header__menu--navLink:hover {
  color: #8d81ac;
}
.header__menu--btn {
  width: 145px;
  height: 38px;
  border-radius: 100px;
  color: #ffffff;
  font-family: 'Montserrat';
  font-size: 16px;
  font-weight: 700;
  line-height: 15px;
  cursor: pointer;
  background-color: #8d81ac;
}
.header__menu--btn:hover {
  background-color: #9558A8;
}
/* Fork */
.fork {
  background: url(../../build/img/section-bg.png) no-repeat;
  background-size: cover;
  background-position: center;
  height: 800px;
}
.fork__main {
  text-align: center;
  padding: 85px 0;
}
.fork__main h1 {
  color: #ffffff;
  font-family: 'Montserrat';
  font-size: 82px;
  font-weight: 700;
  line-height: 20.45px;
  margin-bottom: 60px;
}
.fork__main--world {
  color: #8d81ac;
  font-family: 'Lato';
  font-size: 30px;
  font-weight: 400;
  line-height: 20.45px;
}
.fork__main--mac {
  color: #848e97;
  font-family: 'Montserrat';
  font-size: 16px;
  font-weight: 400;
  line-height: 20.45px;
  text-transform: uppercase;
}
/* Editor */
.editor {
  height: 390px;
}
.editor__main {
  margin: -30px auto;
  max-width: 390px;
}
.editor__main button {
  width: 390px;
