  border-radius: 100px;
  color: #ffffff;
  font-family: 'Montserrat';
  font-size: 21px;
  font-weight: 700;
  line-height: 20.45px;
  cursor: pointer;
  background-color: #8d81ac;
}
.editor__main button:hover {
  background-color: #9558A8;
}
.editor__main p {
  color: #848e97;
  font-family: 'Montserrat';
  font-size: 14px;
  font-weight: 400;
  line-height: 15px;
