/* News */
.news {
  height: 134px;
  background-color: #f4f4f4;
}
.news__main {
  margin: 0 auto;
  max-width: 1110px;
  padding: 40px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.news__main h4 {
